.header {
  position: relative;
  width: 100%;
  color: $color-text;
  font-size: 1.6rem;
  background-color: $color-bg-header;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 2rem;
  margin-top: 7.4rem;

  @include tablet {
    margin-top: 6rem;
  }

  @include desktop {
    margin-top: 6.8rem;
  }

  &__container {
    flex-basis: 100%;
    text-align: center;

    @include tablet {
      position: absolute;
      top: 30%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80rem;
    }
  }
  &__slogan {
    font-size: 4.7vw;
    margin: 7vh 0 1.8rem;
    color: $color-text;

    @include tablet {
      font-size: 2.8rem;
      margin: 0;
    }

    @include desktop {
      font-size: 2.5vw;
    }
  }

  &__backgroundContainer {
    flex-basis: 100%;
    display: flex;
    justify-content: space-between;
  }

  &__background--left,
  &__background--right {
    margin-top: -6rem;
    max-width: 50%;
    display: block;
    height: auto;
    object-fit: cover;

    @include tablet {
      max-width: 35%;
    }

    @include desktop {
      max-width: 700px;
    }
  }

  &__buttonContainer {
    display: flex;
    flex-basis: 100%;
    margin: 0 6% 2rem;

    @include tablet {
      position: absolute;
      top: 65%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin: 0;
    }
  }

  &__btn {
    display: block;
    border: 1px solid $color-border-btn;
    border-radius: 5rem;
    background-color: $color-white;
    flex-basis: 100%;
    max-width: 350px;
    margin: 0.7rem auto;

    .link {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1.6rem 2rem;
      font-size: 1.6rem;
      color: $color-text;
      font-weight: 600;

      @include tablet {
        font-size: 2.2rem;
        padding-left: 2.5rem;
      }

      p {
        display: inline-block;
        padding-right: 1rem;
        transition: all 0.2s ease-in-out;

        @include tablet {
          padding-right: 2rem;
        }
      }
    }
  }

  &__btn:hover p {
    color: $color-blue;
  }
}
